import { Component, OnInit } from '@angular/core';
import {Input, Output, EventEmitter} from '@angular/core';

@Component({
  selector: 'app-ipop',
  templateUrl: './ipop.component.html',
  styleUrls: ['./ipop.component.css']
})
export class IpopComponent implements OnInit {

  constructor() {    
   }
   @Input('id')pid;
   @Input('name')pname;
   @Input('cost')pcost;

   @Output() sendData:EventEmitter <any> =new EventEmitter();

   public clickMe(){
   this.sendData.emit( this.pid+" "+this.pname+" "+this.pcost);
  }
  ngOnInit() {
  }

}
