import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AboutusComponent } from './aboutus/aboutus.component';
import { ContactusComponent } from './contactus/contactus.component';
import { HomeComponent } from './home/home.component';
import { Abt1Component } from './abt1/abt1.component';
import { Abt2Component } from './abt2/abt2.component';
import { PathnotComponent } from './pathnot/pathnot.component';
import { CustomerModule } from './customer/customer.module';
import { ServiceComponent } from './service/service.component';
import { ParentComponent } from './parent/parent.component';
import {MyserviceService} from './myservice.service';
import { OneComponent } from './one/one.component';
import { myDirective } from './cusdir/cusdir.component'

const routes: Routes = [
{path:'hm',component:HomeComponent},
{path:'about',component:AboutusComponent,children:[{path:'abt1',component:Abt1Component,},{path:'abt2',component:Abt2Component}],canActivateChild:[MyserviceService]},
{path:"contact",component:ContactusComponent},
{path:'one',component:OneComponent},
{path:'customd',component:myDirective},
{path:"service",component:ServiceComponent,canDeactivate:[MyserviceService]},
{path:"parent",component:ParentComponent,canActivate:[MyserviceService]},
{path:'lazy',loadChildren:'./customer/customer.module#CustomerModule'},
{path:'',redirectTo:'hm',pathMatch:'full'},
{path:'**',component:PathnotComponent}

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
